<template>
    <div class="counter">
        <div class="wrapper">
            <h1>{{count}} <span>to go</span></h1>
            <audio class="prayerAudio" preload="auto" controls>
                    <source :src="audio.src" type="audio/mpeg">
                    Your browser does not support the audio element.
            </audio>
            <p class="prayer">O Jesus, I surrender myself to You, take care of everything!</p>
            <a @click="countdown" class="link">{{linkText}}</a>
        </div><!--wrapper-->
    </div><!--Counter-->
</template>

<script>
    //npm i jquery  
    // import $ from 'jquery'

    export default{
        name: 'Counter',
        props:{
            day: String,
        },
        data(){
            return{
                count: 10,
                index: 0,
                linkText: 'Next',
                audio: require("../assets/sounds/surrender1.mp3"),
                audios: [{
                        id: 1,
                        src: require("../assets/sounds/surrender1.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    },
                    {
                        id: 2,
                        src: require("../assets/sounds/surrender2.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    },
                    {
                        id: 3,
                        src: require("../assets/sounds/surrender3.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    },
                    {
                        id: 4,
                        src: require("../assets/sounds/surrender4.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    },
                    {
                        id: 5,
                        src: require("../assets/sounds/surrender5.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    },
                    {
                        id: 6,
                        src: require("../assets/sounds/surrender6.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    },
                    {
                        id: 7,
                        src: require("../assets/sounds/surrender7.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    },
                    {
                        id: 8,
                        src: require("../assets/sounds/surrender8.mp3"),
                        alt: "O Jesus, I surrender myself to you, take care of everything!"
                    }
                ]
            }
        },
        mounted(){
            this.getAudio();
        },
        methods:{
            countdown(){
                let nextDay = Number(this.$props.day) + 1;
                if (this.count > 1){
                    document.querySelector('.prayerAudio').pause();
                    document.querySelector('.prayerAudio').currentTime = 0;
                    this.getAudio();
                    document.querySelector('.prayerAudio').load();
                    setTimeout(() => {
                        this.count--;
                    }, 250);
                } else if (this.count === 1){
                    document.querySelector('.counter .wrapper h1').innerHTML= 'Your prayers have been heard.<br/>God Bless!';
                    document.querySelector('.prayerAudio').style.display = "none";
                    this.linkText = 'Done';
                    if(Number(this.$props.day) >= 9){
                        document.querySelector('.prayer').innerHTML= "Congratulations! You have completed the Surrender Novena!";
                    } else{
                        document.querySelector('.prayer').innerHTML= "Return tomorrow to say the prayer for day "+nextDay;
                    }
                    document.querySelector('.prayerAudio').pause();
                    this.count--;
                } else if (this.count < 1){
                    window.scrollTo(0,0);
                    this.$router.push('/surrender-prayer/');
                }
                this.$ga.event('clicks', 'counter', 'counter link', 125);
            },
            getAudio(){
                this.audio = this.audios[this.index];
                this.index = Math.floor(Math.random() * (this.audios.length - 1)) + 1;
            }
        }
    }
</script>

