<template>
  <div class="menu">
    <router-link id="homeButton" class="menuButton" :to="go" @click.native="analyticsHome"><font-awesome-icon :icon="['fas', 'home']" /></router-link>
    <router-link id="bookButton" class="menuButton" :to="book" @click.native="analyticsBook"><font-awesome-icon :icon="['fas', 'book']" /></router-link>
  </div>
</template>

<script>
export default {
  name: 'Menu',
  props:{
      go: String,
      book: String
  },
  methods:{
    analyticsHome(){
      this.$ga.event('clicks', 'home', 'home icon', 120);
    },
    analyticsBook(){
      this.$ga.event('clicks', 'book', 'book icon', 121);
    }
  }
}
</script>

